import React from 'react';
import 'isomorphic-fetch';

class Channel extends React.Component{
    
    static async getInitialProps({ query }){
        const id = query.id
        const res = await fetch(`https://api.audioboom.com/channels/${ id }`);
        const {body: chanel} = await res.json();
        return {chanel: chanel};
    }
    
    render(){
        const { chanel } = this.props;
        console.log(chanel);
        return(
            <div>
                <img src={chanel.urls.banner_image.original} alt=""/>
            </div>
        )
    }
}

export default Channel;